@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700;800&display=swap);
:root {
  --grey: hsla(0, 0%, 37%, 1);
  --black: hsla(0, 0%, 20%, 1);
  --blue: hsla(201, 50%, 43%, 1);
  --dark-green: hsla(194, 63%, 27%, 1);
  --light-green: hsla(194, 30%, 62%, 1);
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%; /* default 10px */
  font-size: 80%;
}

body {
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 0.9rem;
  font-weight: 400;
  line-height: 1.5;
  padding: 20px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-size: 2.7rem;
  letter-spacing: 0.05em;
  font-weight: 800;
  margin: 0;
}

h2 {
  font-size: 1.4rem;
  font-weight: 400;
  margin: 0;
}

h3 {
  font-size: 1.1rem;
  font-weight: 600;
  letter-spacing: 0.05em;
  margin: 0;
}

h4 {
  font-size: 0.9rem;
  font-weight: 600;
  margin: 0;
}

.contact-details {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 6px;
  gap: 6px;
}

.details-text {
  font-size: 0.8rem;
}

img {
  width: 14px;
}

header {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-bottom: 3px solid var(--light-green);
  padding-bottom: 12px;
}

h1 {
  color: var(--dark-green);
  text-transform: uppercase;
}

h2 {
  color: var(--grey);
}

.heading {
  display: flex;
  flex-direction: column;
}

.details-group {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  grid-gap: 7px;
  gap: 7px;
}

.cv__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 40px;
  gap: 40px;
}

.cv__view {
  padding: 50px;
  width: 595px;
  min-width: 595px;
  height: 842px;
  border-radius: 2px;
  border: 1px solid black;
  box-shadow: 5px 0 6px 0 hsl(0 0% 0% / 0.2);

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  grid-gap: 16px;
  gap: 16px;
}

.cv__form {
  width: 595px;
}

.experience {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  grid-gap: 2px;
  gap: 2px;
}

.experience__heading {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

h4,
.experience__details {
  color: var(--grey);
}

.experience__details {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  grid-gap: 2px;
  gap: 2px;
}

.bold {
  font-weight: 600;
}

.experience-list {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  grid-gap: 8px;
  gap: 8px;
}

h3 {
  text-transform: uppercase;
  color: var(--blue);
}

/* Reset */
input,
textarea,
button {
  font: inherit;
  color: inherit;
  border: none;
  background: none;
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
}

/* Styling */
form {
  border: 2px solid var(--dark-green);
  border-radius: 5px;
  padding: 30px;
  display: flex;
  flex-direction: column;
  grid-gap: 25px;
  gap: 25px;
}

h2 {
  margin-bottom: 5px;
}

label {
  text-transform: capitalize;
  display: flex;
  flex-direction: column;
}

input::-webkit-input-placeholder {
  text-transform: capitalize;
}

input:-ms-input-placeholder {
  text-transform: capitalize;
}

input::placeholder {
  text-transform: capitalize;
}

input,
textarea {
  border: 1px solid var(--grey);
  border-radius: 3px;
  font-size: 1rem;
  padding: 5px;
}

textarea {
  width: 100%;
  resize: none;
}

.group {
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
}

.group:not(:last-child) {
  margin-bottom: 20px;
}

.hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.btn-group {
  display: flex;
  grid-gap: 8px;
  gap: 8px;
}

button {
  color: var(--black);
  border: 1px solid var(--grey);
  padding: 4px 12px;
  border-radius: 5px;
  font-size: 0.9rem;
  font-weight: 600;
  transition: all 150ms;
}

.btn--add {
  color: var(--dark-green);
}

button:hover {
  transform: translateY(-2px);
  box-shadow: 0 2px 3px 0 hsl(0 0% 0% / 0.1);
}

.group:not(:last-child) button.btn--add {
  display: none;
}

